.custom-style-checkbox,
.custom-style-checkbox-left,
.custom-style-checkbox-right {
    input[type="checkbox"] {
        opacity: 0;

        + label::after {
            content: none;
        }

        &:checked + label::after {
            content: "";
        }
    }
}

.custom-style-checkbox{
    label {
        position: relative;
        display: inline-block;
        padding-left: 22px;
        font-family: 'Apercu Pro';
        font-weight: 600;
        font-size: 13px;
        line-height: 20px;
        color: #000000;

        &:before,
        &:after {
            position: absolute;
            content: "";
            display: inline-block;
        }

        &:before{
            height: 16px;
            width: 16px;
            border: 1px solid #1D3963;
            left: 0px;
            top: -4px;
            border-radius: 2px;
        }

        &:after {
            height: 5px;
            width: 9px;
            border-left: 1px solid #1D3963;
            border-bottom: 1px solid #1D3963;
            transform: rotate(-45deg);
            left: 4px;
            top: 0;
        }
    }
}

.custom-style-checkbox-left {
    label {
        position: relative;
        display: inline-block;
        padding-left: 22px;
        font-family: 'Apercu Pro';
        font-weight: 600;
        font-size: 13px;
        line-height: 20px;
        color: #000000;

        &:before,
        &:after {
            position: absolute;
            content: "";
            display: inline-block;
        }

        &:before{
            height: 16px;
            width: 16px;
            border: 1px solid #1D3963;
            left: 0px;
            top: 2px;
            border-radius: 2px;
        }

        &:after {
            height: 5px;
            width: 9px;
            border-left: 1px solid #1D3963;
            border-bottom: 1px solid #1D3963;
            transform: rotate(-45deg);
            left: 4px;
            top: 6px;
        }
    }
}

.custom-style-checkbox-right {
    label {
        position: relative;
        display: inline-block;
        padding-right: 22px;
        font-family: 'Apercu Pro';
        font-weight: 600;
        font-size: 13px;
        line-height: 20px;
        color: #000000;

        &:before,
        &:after {
            position: absolute;
            content: "";
            display: inline-block;
        }

        &:before{
            height: 16px;
            width: 16px;
            border: 1px solid #1D3963;
            right: 0px;
            top: 2px;
            border-radius: 2px;
        }

        &:after {
            height: 5px;
            width: 9px;
            border-left: 1px solid #1D3963;
            border-bottom: 1px solid #1D3963;
            transform: rotate(-45deg);
            right: 4px;
            top: 6px;
        }
    }
}


.select-dropdown-with-clear-btn {
    position: relative;

    select {
        -webkit-appearance: none;
        padding-right: 40px;
    }

    .clear-button {
        position: absolute;
        bottom: 10px;
        right: 20px;
        font-weight: 500;
        font-size: 13px;
        line-height: 20px;
        text-decoration-line: underline;
        color: #1D3963;
        cursor: pointer;
    }
}

.btn-submit {
    background: #1D3963;
    border: 1px solid #1D3963;
    box-sizing: border-box;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: 500;
    font-size: 13px;
    line-height: 18px;
    color: #FFFFFF;
    @extend .transition;

    &:hover {
        color: $white;
        box-shadow: 0px 0px 10px 5px rgba(#1D3963, 0.4);
    }
}

.form-control {
    height: 40px;
    border-radius: 5px;
    color: $black;
    &:focus {
        border-color: #1D3963;
        box-shadow: 0 0 0 0.2rem rgba(#1D3963, 0.25);
    }
}

.terms-with-number {
    position: relative;
    min-height: 25px;
    .number {
        font-family: 'Inter', sans-serif;
        background: #E6E6E6;
        border-radius: 6px;
        padding: 6px;
        min-width: 27px;
        font-size: 11px;
        line-height: 11px;
        color: #675B5B;
        display: inline-block;
        text-align: center;
        margin-right: 5px;
        border: 1px solid #E6E6E6;
    }

    .terms-text {
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 20px;
        color: #675B5B;
    }

    &.terms-primary {
        .number {
            color: #3A71C6;
            border: 1px solid #3A71C6;
        }

        .terms-text {
            color: #3A71C6;
        }
    }
}

.text-underline {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 11px;
    line-height: 15px;
    text-decoration-line: underline;
}

.checklist-table {
    border: 1px solid #e9ecef;
    thead {
        background: #F8F8F8;
        td {
            border-top: 0;
        }
    }

    th, td {
        vertical-align: middle;
    }
}

.font-600 {
    font-weight: 600 !important;
}

.circle-checkbox {
    height: 28px;
    width: 28px;
    background-image: url(../img/need-checkbox.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin: 0 auto;
    cursor: pointer;

    &.done {
        background-image: url(../img/done-checkbox.svg);
        cursor: initial;
    }

    &.pending {
        background-image: url(../img/pending-checkbox.svg);
        cursor: initial;
    }

    &.not-needed {
        background-image: url(../img/not-needed-checkbox.svg);
        cursor: initial;
    }
}

.small-text {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    color: #A5A5A5;
}

.blue-border-left {
    border-left: 5px solid #3A71C6;
}

.green-border-left {
    border-left: 5px solid #0EB71F;
}

.w-110 {
    width: 110px;
}

.w-150 {
    width: 150px;
}

// Modal
.custom-checklist-modal {
    background: rgba($white, 0.9);
    padding-left: 0 !important;
}

.modal-dialog {
    margin: 102px auto 50px;
    &.small-modal {
        max-width: 419px;
    }

    &.medium-modal {
        max-width: 815px;
    }

    .modal-content {
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
        border: 0;
        border-radius: 6px;
    }
}

.modal-content-title {
    font-family: 'Apercu Pro';
    font-weight: normal;
    font-size: 24px;
    line-height: 30px;
    color: #3A71C6;
}

.market-wrapper {
    .market-name {
        font-family: 'Inter', sans-serif;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #101010;
        padding-bottom: 5px;
    }

    .location-name {
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #101010;
        padding-bottom: 10px;
    }

    .event-date-time {
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #A5A5A5;
    }
}

.modal-content-box {
    font-family: 'Apercu Pro';
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    color: #000000;
}

.modal-form-title,
.modal-form-input {
    font-family: 'Apercu Pro';
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    color: #000000;
}

.modal-form-input,
.modal-form-text-area {
    &::placeholder {
        color: rgba(0, 0, 0, 0.2);
        opacity: 1;
    }

    &.required {
        border-color: $tmpcolor;
        color: $tmpcolor;

        &::placeholder {
            color: $tmpcolor;
            opacity: 1;
        }
    }
}

.accepted-text {
    color: #0EB71F;
}

.rejected-text {
    color: #D03833;
}

.pending-text {
    color: #8B8B8B;
}

.needed-text {
    color: #466FFF;
}

.modal-highlighted-text {
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    color: #101010;
}

// Modal End

@media (max-width: 1199px) {
    .terms-with-number {
        position: relative;
        .number {
            position: absolute;
        }
    
        .terms-text {
            padding-left: 35px;
            display: inline-block;
            text-align: left;
        }
    }

    .search-wrapper {
        .w-40 {
            width: 35% !important;
        }

        .w-10 {
            width: 15% !important;
        }
    }
}

@media (max-width: 767px) {
    .search-wrapper {
        .w-25,
        .w-40,
        .w-10 {
            width: 50% !important;
        }
    }
}