.checklist-common-btn {
    font-family: 'Apercu Pro';
    font-weight: 600;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    display: block;
    background: #1D3963;
    border-color: #1D3963;
    padding: 9px 15px;
    border-radius: 5px;
    @extend .transition;

    &:hover, &:focus {
        color: #FFFFFF;
        box-shadow: 0px 0px 10px 5px rgba(#1D3963, 0.4);
    }

    &.btn-outline-primary {
        border-color: #1D3963;
        color: #1D3963;
        background: #FFFFFF;

        &:hover {
            color: #FFFFFF !important;
            background: #1D3963 !important;
        }
    }

    &.secondary-btn {
        background: #8e9cb1;
        border-color: #8e9cb1;
    }
}